import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Dish from '@/model/Dish'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    tableConfig: {},
  },
  dishName: {

  },
}

const config = {
  load: async function () {
    const allDishes = await Dish.getList()
    const res = allDishes.filter(i => ['-19', '-20'].indexOf(i.categoryId.toString()) >= 0)
    console.log('pfand', res)
    return res
  },
  edit: function (item) {
    item.appliedAttributeGroup = item.attributeGroupId
    item.catTypeId = item.dishesCategoryTypeId

    return hillo.postWithUploadFile('Category.php?op=update', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  add: async function () {

  },
  remove: async function () {

  },

}

export default IKDataEntity.ModelFactory(entity, config)
